	module.exports = {"data":{"post":{"html":"<p>Today is the 65th anniversary of the <a title=\"KRISTALLNACHT\" href=\"http://www.mtsu.edu/~baustin/knacht.html\">Kristallnacht</a>, the 'night of broken glass' when Nazi mobs destroyed 101 synagouges and thousands of Jewish businesses. Since the fall of the Berlin Wall in1989 (also on this date) and the surge of Jewish immigrants from Russia, many synagouges are being rebuilt. Today the <a title=\"Grundsteinlegung | Bayern heute | Bayerischer Rundfunk\" href=\"http://www.br-online.de/bayern-heute/artikel/0311/09_grundsteinlegung/index.xml\">cornerstone was laid</a> for a new Jewish center in Munich.</p>\n<p><a title=\"vowe dot net :: Synagogues in Germany - A Virtual Reconstruction\" href=\"http://vowe.net/archives/003793.html\">vowe</a> and <a title=\"Industrial Technology & Witchcraft - das Weblog von TextLab\" href=\"http://www.industrial-technology-and-witchcraft.de/index.php?id=P3738\">itw</a> both point to the <a title=\"Synagogues in Germany - A Virtual Reconstruction\" href=\"http://www.cad.architektur.tu-darmstadt.de/synagogen/inter/en_menu.html\">Synagogues in Germany project</a>, in which synagouges are being vitually reconstructed by architecture students and made viewable online.</p>","fields":{"title":"Virtual Synagouges","date":"09 Nov 2003","url":"https://papascott.de/2003-11-09-virtual-synagouges/"}}},"pathContext":{"slug":"/2003-11-09-virtual-synagouges/","prev":{"fields":{"title":"SecurityFocus RSS Feeds","date":"09 Nov 2003","slug":"/2003-11-09-securityfocus-rss-feeds/"}},"next":{"fields":{"title":"McDonald's anger over McJob dictionary entry","date":"10 Nov 2003","slug":"/2003-11-10-mcdonalds-anger-over-mcjob-dictionary-entry/"}}}}

